// import { Metamask } from 'assets/imgs'
import React from 'react'
import styled from 'styled-components'
import Button from 'templates/Button/Button'

const ButtonWrapper = styled(Button)`
  height: 56px;
  padding: 0 20px;
  justify-content: space-between;
`

const ImgWrapper = styled.img`
  width: 25px;
  height: 25px;
`

export default function Option({
  link = null,
  clickable = true,
  size,
  onClick,
  color,
  header,
  subheader = null,
  icon,
  active = false,
  id
}: {
  link?: string | null
  clickable?: boolean
  size?: number | null
  onClick?: () => void
  color: string
  header: React.ReactNode
  subheader: React.ReactNode | null
  icon: string
  active?: boolean
  id: string
}) {
  const content = (
    <ButtonWrapper type="primary" shadow={clickable ? true : undefined} id={id} onClick={onClick}>
      {header}
      <ImgWrapper src={icon} alt={'Icon'} />
    </ButtonWrapper>
  )
  if (link) {
    return (
      <ButtonWrapper
        type="primary"
        shadow
        id={id}
        onClick={() => {
          window.open(link)
        }}
      >
        <span>{header}</span>
        <ImgWrapper src={icon} alt={'Icon'} />
      </ButtonWrapper>
    )
  }
  return content
}
